

















































/** transition-group 动画 */
.item-enter{
    -webkit-transform: translateX(300px);
            transform: translateX(300px);
}
.item-enter-active{
    -webkit-transition: all .5s;
    transition: all .5s;
}
.item-leave-active{
    opacity: 0;
    position: absolute;
}
.item-move{
    -webkit-transition: all 1s;
    transition: all 1s;
}
/** end */
.todo-list{
    width: 60%;
    list-style: none;
    margin-top: 30px;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 10px;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    position: relative;
}
.todo-list .todo-item{
    width: 100%;
    height: 35px;
    line-height: 35px;
    color: #3e434f;
    font-size: 16px;
    margin-bottom: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.todo-list .todo-text{
    width: 100%;
}
.todo-list .todo-finish{
    color: #999;
    text-decoration: line-through;
}
.todo-list .btn-container{
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.todo-list .btn-container button{
    width: 80px;
    height: 25px;
    border-radius: 20px;
    border: none;
    outline: none;
    cursor: pointer;
}
.todo-list .finished-btn{
    background-color: #edf5f8;
}
.todo-list .delete{
    color: #fff;
    background-color: #e72564;
}



